let Dukpt = require('../node-dukpt/dist/index');

let dukpt = new Dukpt('0123456789ABCDEFFEDCBA9876543210', 'FFFFFFFFFFFFFFFFFFFF');

console.log(dukpt.dukptEncrypt('%B4815881002861896^YATES/EUGENE JOHN              ^37829821000123456789?', {
    inputEncoding: 'ascii',
    encryptionMode: 'AES'
}));

console.log(dukpt.dukptDecrypt('6773ECC682BA7419F7035B4097BF4052D0460D90165651F7CA6760E612F422DA68D6385D2F5705B5F5A8A2DABEA93BEA157ED634E0729923FD8F720985F3624D06FAA7B133883B8FA5860294FAF36F80', {
    outputEncoding: 'ascii',
    decryptionMode: 'AES'
}));

console.log(dukpt.dukptEncrypt('%B4815881002861896^YATES/EUGENE JOHN              ^37829821000123456789?', {
    inputEncoding: 'ascii',
    encryptionMode: '3DES'
}));

